var bip39 = require('bip39');

var hdkey = require('ethereumjs-wallet/hdkey');

var keythereum = require('keythereum');

exports.debug = false;

function getPrivateKeyFromMnemonic(mnemonic) {
  var hdwallet = hdkey.fromMasterSeed(bip39.mnemonicToSeed(mnemonic));
  var wallet_hdpath = "m/44'/60'/0'/0/";
  var wallet = hdwallet.derivePath(wallet_hdpath + '0').getWallet();

  var privateKey = wallet._privKey.toString('hex');

  var account = '0x' + wallet.getAddress().toString('hex').toUpperCase();
  if (exports.debug) console.log({
    mnemonic: mnemonic,
    privateKey: privateKey,
    account: account
  });
  return privateKey;
}

exports.getPrivateKeyFromMnemonic = getPrivateKeyFromMnemonic;

function getPrivateKeyFromRandom() {
  var params = {
    keyBytes: 32,
    ivBytes: 16
  };
  var dk = keythereum.create(params);
  var privateKey = dk.privateKey.toString('hex');
  if (exports.debug) console.log({
    privateKey: privateKey,
    salt: dk.salt.toString('hex'),
    iv: dk.iv.toString('hex')
  });
  return privateKey;
}

exports.getPrivateKeyFromRandom = getPrivateKeyFromRandom;

function getPrivateKeyFromKeystore(keystore, password) {
  var privateKeyBuf = keythereum.recover(password, keystore);
  var privateKey = privateKeyBuf.toString('hex');
  if (exports.debug) console.log({
    privateKey: privateKey,
    password: password,
    keystore: keystore
  });
  return privateKey;
}

exports.getPrivateKeyFromKeystore = getPrivateKeyFromKeystore;

function getKeystoreFromPrivateKey(privateKey, password) {
  var params = {
    keyBytes: 32,
    ivBytes: 16
  };
  var dk = keythereum.create(params);
  var options = {
    kdf: 'pbkdf2',
    cipher: 'aes-128-ctr',
    kdfparams: {
      c: 262144,
      dklen: 32,
      prf: 'hmac-sha256'
    }
  }; // const keystore = keythereum.dump(password, dk.privateKey, dk.salt, dk.iv, options);

  var keystore = keythereum.dump(password, privateKey, dk.salt, dk.iv, options);

  if (exports.debug) {
    console.log({
      privateKey: privateKey,
      password: password,
      keystore: keystore
    });
  }

  return keystore;
}

exports.getKeystoreFromPrivateKey = getKeystoreFromPrivateKey;